<template>
    <div class="demo-tab">
        <demo-row title="基本用法">
            <demo-line label="show tab2">
                <pl-checkbox v-model="showFlag" label="动态 销毁/初始化 一个tab不会影响顺序"/>
            </demo-line>
            <pl-tab-group v-model="val[0]">
                <pl-tab title="用户管理" val="user management">
                    用户管理
                    <input type="text">
                </pl-tab>
                <pl-tab title="子模块数据管理" val="submodule management" v-if="showFlag" init>
                    子模块数据管理
                    <input type="text">
                </pl-tab>
                <pl-tab title="数据集" val="role management">
                    数据集
                    <input type="text">
                </pl-tab>
            </pl-tab-group>
        </demo-row>
        <demo-row title="超长列表">
            <pl-tab-group>
                <pl-tab v-for="item in 40" :key="item" :title="`页签 ${item}`">
                    {{`tab ${item}`}}
                </pl-tab>
            </pl-tab-group>
        </demo-row>
        <demo-row title="三种样式">
            <pl-tab-group v-for="item in ['text','card','fillet']" :key="item" :headType="item" v-model="bindValue">
                <pl-tab title="用户管理" val="user management">用户管理</pl-tab>
                <pl-tab title="子模块数据管理" val="submodule management">子模块数据管理</pl-tab>
                <pl-tab title="数据集" val="role management">数据集</pl-tab>
            </pl-tab-group>
        </demo-row>

        <demo-row title="选项卡位置">
            <demo-line title="位置">
                <pl-button-group>
                    <pl-button v-for="item in positions" :key="item" :label="item" :active="position === item" @click="position = item"/>
                </pl-button-group>
            </demo-line>
            <demo-line title="样式">
                <pl-button-group>
                    <pl-button v-for="item in types" :key="item" :label="item" :active="type === item" @click="type = item"/>
                </pl-button-group>
            </demo-line>
            <demo-line>
                <pl-checkbox label="显示关闭图标" v-model="showClose"/>
            </demo-line>
            <pl-tab-group style="height:300px" :headPosition="position" :key="`${position}-${type}`" :headType="type" :closeIcon="showClose">
                <pl-tab title="用户管理" val="user management">
                    用户管理
                </pl-tab>
                <pl-tab title="子模块数据管理" val="submodule management">
                    子模块数据管理
                </pl-tab>
                <pl-tab title="数据集" val="role management">
                    数据集
                </pl-tab>
            </pl-tab-group>
            <pl-tab-group style="height:300px" :headPosition="position" :key="`${position}-${type}`" :headType="type" :closeIcon="showClose">
                <pl-tab v-for="item in 40" :key="item" :title="`页签 ${item}`">
                    {{`tab ${item}`}}
                </pl-tab>
            </pl-tab-group>
        </demo-row>

        <demo-row title="动态增删标签">
            <demo-line title="操作按钮">
                <pl-button-group>
                    <pl-button label="add" @click="counts.push(record++)"/>
                    <pl-button label="minus" @click="counts.shift()"/>
                </pl-button-group>
                <pl-toggle v-model="show"/>
            </demo-line>
            <pl-tab-group closeIcon @close="onClose">
                <pl-tab v-for="item in counts" :key="item" :title="`页签 ${item}`" :val="item">
                    {{`tab ${item}`}}
                </pl-tab>
            </pl-tab-group>
        </demo-row>

        <demo-row title="页签初始化控制">
            <pl-tab-group>
                <pl-tab title="用户管理" val="user management">
                    用户管理
                    <input type="text">
                </pl-tab>
                <pl-tab title="子模块数据管理:立即初始化" val="submodule management" v-if="showFlag" init>
                    子模块数据管理
                    <input type="text">
                </pl-tab>
                <pl-tab title="数据集:关闭的时候销毁内容" val="role management" destroyOnHide>
                    数据集
                    <input type="text">
                </pl-tab>
            </pl-tab-group>
        </demo-row>

        <demo-row title="自定义选项卡标题">
            <pl-tab-group>
                <pl-tab val="user management">
                    <div slot-scope="{active}" slot="head" v-tooltip="'用户管理'">
                        <span>用户管理</span>
                        <pl-icon :icon="active?'el-icon-user-solid':'el-icon-user'"/>
                    </div>
                    用户管理
                    <input type="text">
                </pl-tab>
                <pl-tab val="submodule management" v-if="showFlag" init>
                    <div slot-scope="{active}" slot="head" v-tooltip="'子模块数据管理'">
                        <span>子模块数据管理</span>
                        <pl-icon :icon="active?'el-icon-warning':'el-icon-warning-outline'"/>
                    </div>
                    子模块数据管理
                    <input type="text">
                </pl-tab>
                <pl-tab val="role management" destroyOnHide>
                    <div slot-scope="{active}" slot="head" v-tooltip="'数据集'">
                        <span>数据集</span>
                        <pl-icon :icon="active?'el-icon-message-solid':'el-icon-bell'"/>
                    </div>
                    数据集
                    <input type="text">
                </pl-tab>
            </pl-tab-group>
        </demo-row>

    </div>
</template>

<script>
    export default {
        name: "demo-tab",
        props: {},
        data() {
            return {
                positions: ['top', 'bottom', 'left', 'right'],
                types: ['text', 'card', 'fillet'],

                position: 'top',
                type: 'text',

                val: {},

                counts: [1, 2, 3],
                record: 4,

                showClose: false,
                show: true,
                showFlag: true,

                bindValue: 'user management',
            }
        },
        methods: {
            onClose(item, index) {
                this.counts.splice(index, 1)
            },
        },
    }
</script>

<style lang="scss">
</style>